.footer-list__item-link {
    display: flex;
    gap: 14px;
    align-items: center;
    color: #6c63ff;
    text-decoration: none;
    padding: 8px;
    border-bottom: 2px solid #6c63ff;
    transition: opacity 0.3s ease;
  }
  
  .footer-list__item-link:hover {
    opacity: 0.6;
  }
  